import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import swiper from '@/components/swiper'
import touch from '@/components/touch'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/',
      name: 'swiper',
      component: swiper
    }, {
      path: '/touch',
      name: 'touch',
      component: touch
    }
  ]
})



